export default {
  name: "discover",
  data() {
    return {
      option: {
        firstList: [
          { name: "学员见证", class: "", type: true },
          { name: "作业赏析", class: "", type: false }
        ]
      }
    };
  },
  //   直接执行
  mounted() {},
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    openDetailsOfTraineesWitnesses: function() {
      this.$router.push({ path: "/discover/studentDetails" });
    },
    openHomeworkAppreciation: function() {
      this.$router.push({ path: "/discover/jobDetails" });
    }
  }
};



